<template>
    <div id="alarm-list">
        <el-table :data="tableData" :header-cell-style="{background:'#eef1f6',color:'#606266',border:'1px solid #DFE1E4'}" :span-method="arraySpan">
            <el-table-column prop="id" label="规则id"></el-table-column>
            <el-table-column prop="metrics" label="监控指标"></el-table-column>
            <el-table-column label="阈值" header-align="center">
                <el-table-column prop="threshold.warning" label="提示"></el-table-column>
                <el-table-column prop="threshold.serious" label="严重"></el-table-column>
                <el-table-column prop="threshold.mortal" label="致命"></el-table-column>
            </el-table-column>
            <el-table-column prop="granularity" label="粒度"></el-table-column>
            <el-table-column prop="email" label="联系邮箱"></el-table-column>
            <el-table-column prop="create_time" label="创建时间"></el-table-column>
        </el-table>
    </div>
</template>

<script>
import { alarmItem, ruleGetting } from '../../request/api'
export default {
    data() {
        return {
            tableData: [],
        };
    },
    mounted() {
        this.initData()
    },
    methods: {
        async initData() {
            let res = await ruleGetting()
            let data = res.data
            // console.log(data)
            if (data.CODE != 200) {
                console.log(data.msg)
                return false
            }
            for (let i = 0; i < data.DATA.length; i++) {
                let { 
                    ID,
                    CpuNoteworthyThreshold,
                    CpuSeriousThreshold,
                    CpuDeadlyThreshold,
                    MemNoteworthyThreshold,
                    MemSeriousThreshold,
                    MemDeadlyThreshold,
                    ContactEmail,
                    Granularity,
                    CreatedAt } = data.DATA[i]

                this.tableData.push({
                    id:ID,
                    metrics: 'cpu使用率',
                    threshold: {
                        warning: CpuNoteworthyThreshold,
                        serious: CpuSeriousThreshold,
                        mortal: CpuDeadlyThreshold,
                    },
                    granularity:Granularity,
                    email: ContactEmail,
                    create_time: this.timefilter(CreatedAt) 
                })
                this.tableData.push({
                    id:ID,
                    metrics: '内存使用率',
                    threshold: {
                        warning: MemNoteworthyThreshold,
                        serious: MemSeriousThreshold,
                        mortal: MemDeadlyThreshold,
                    },
                    granularity:Granularity,
                    email: ContactEmail,
                    create_time: this.timefilter(CreatedAt) 
                })
            }
        },
        timefilter(timeStr){
            let reg = /T|\.|(?<=\.).*/g
            return timeStr.replace(reg," ")
        },
        arraySpan({row,column,rowIndex,columnIndex}){
            if(columnIndex == 0 || columnIndex == 5 || columnIndex ==6 || columnIndex == 7){
                if(rowIndex % 2 === 0) {
                    return [2,1]
                }else{
                    return [0,0]
                }
            }
        }
    }
};
</script>

<style lang="less" scoped>
#alarm-list {
    margin-top: 10px;
}
</style>